<template>
  <img v-on:click="playQuote" v-bind:src="getImage" v-bind:alt="$props.characterName"/>
</template>

<script>
export default {
  props: ['characterFileName', 'characterName', 'soundFileName'],
  name: 'SoundButton',
  data () {
    return {
      series: 'fc-de-kampioenen'
    }
  },
  methods: {
    playQuote: function () {
      const audio = new Audio(require('../assets/fc-de-kampioenen/sound/' + this.$props.soundFileName + '.mp3'))
      audio.play()
    }
  },
  computed: {
    getImage: function () {
      return require('../assets/' + this.$data.series.toString() + '/' + this.$props.characterFileName.toString())
    }
  }
}
</script>

<style scoped>

</style>



